nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 40px;
}
nav a {
  text-decoration: none;
  font-size: 1.2rem;
  margin-right: 3rem;
  color: white;
}
nav a:hover {
  box-sizing: border-box;
  border-bottom: 0.15rem solid white;
}
nav form {
  display: inline;
  margin-left: auto;
}
nav form input {
  font-size: 1rem;
  padding: 0.5rem;
}
nav form button {
  cursor: pointer;
  padding: 0.5rem 0.8rem;
  margin-left: 0.4rem;
  font-size: 1rem;
  border: 0.15rem solid white;
  border-radius: 10px;
  background-color: black;
  color: white;
}
nav form button:hover {
  color: black;
  background-color: white;
  transition: all 0.3s ease;
}
nav #btn:hover {
  border-bottom: none;
}

@media (max-width: 552px) {
  nav {
    padding: 20px 10px;
  }
  nav form input {
    width: 100px;
    height: max-content;
    font-size: 10px;
  }
  nav form button {
    padding: 4px;
  }
}
